@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
body{
  width: 100%;
  height: 100vh;
  font-family: 'Rubik', sans-serif;
}
.main_container{
    width: 100%;
    height: 100vh;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
    overflow: auto;
}
.container{
    position: fixed;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    /* padding: 15px; */
    padding-top: 15px;
    left: 0;
    top: 0;
    right: 0;
}
.header_model_names{
    display: -webkit-flex;
    display: flex;
}
.header_model_names p{
    padding: 5px 15px;
    transition: all .5s;
    border-radius: 20px;
    color: rgb(51, 51, 51);
    font-weight: 500;
    font-size: 15px;
}
.header_img img{
    padding-left: 20px;
}
.header_menu{
    display: -webkit-flex;
    display: flex;
    margin-right: 20px;
}
.account{
    display: -webkit-flex;
    display: flex;
}
.header_menu p{
    padding: 5px 15px;
    transition: all .5s;
    border-radius: 20px;
    color: rgb(51, 51, 51);
    font-weight: 500;
    font-size: 15px;
}
.header_model_names p:hover{
    cursor: pointer;
   background-color: rgba(153, 150, 150, 0.25);
}
.header_menu p:hover{
    cursor: pointer;
   background-color: rgba(153, 150, 150, 0.25);
}
@media only screen and (max-width: 1180px) {
    .header_model_names p{
        display: none;
    }
    .account p{
        display: none;
    }
    .header_menu p{
        padding: 5px 8px;
        background-color: rgba(153, 150, 150, 0.15);;
    }
}
@media only screen and (max-width: 800px) {
    .header_menu p{
        padding: 5px 10px;
    }
}

