.main_info{
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: rgb(51, 51, 51);
    scroll-snap-align: start;
}
.top_info{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 110px;
}
.top_info h1{
    text-align: center;
    letter-spacing: 2px;
    font-size: 40px;
}
.top_info p{
    text-align: center;
    text-transform: capitalize;
}
.down_buttons{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.buttons{
    display: -webkit-flex;
    display: flex;
    padding: 0 10px;
}
.down_buttons p{
    background-color: rgba(42, 42, 42,0.85);
    color: white;
    padding: 8px 60px;
    margin-bottom: 20px;
    border-radius: 20px;
    text-transform: uppercase;
    margin-left: 20px;
}
.down_buttons p:hover{
    cursor: pointer;
}
#existing{
    background-color: rgba(255, 255, 255,0.60);
    color: black;
}
.down_arrow{
    width: 40px;
    -webkit-animation: updown 2s infinite;
            animation: updown 2s infinite;
}
@-webkit-keyframes updown{
    0%, 20%, 50%, 80%, 100%{
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40%{
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
    60%{
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}
@keyframes updown{
    0%, 20%, 50%, 80%, 100%{
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40%{
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
    60%{
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}
@media only screen and (max-width: 768px) {
    .buttons{
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}
@media only screen and (max-width: 420px) {
    .top_info h1{
        font-size: 32px;
    }
}

